
	import {
		IonGrid,
		IonRow,
		IonCol,
		IonText,
		IonPage,
		IonHeader,
		IonToolbar,
		IonTitle,
		IonContent,
		IonButton,

	} from '@ionic/vue';
	import { defineComponent, ref } from 'vue';
    import { useRoute } from 'vue-router';

	export default defineComponent({
		name: 'Error',
		components: {
			IonGrid,
			IonRow,
			IonCol,
			IonText,
			IonPage,
			IonTitle,
			IonHeader,
			IonToolbar,
			IonContent,
			IonButton
		},
        setup() {
            const errorRef = ref<any>({
                code: 500,
                message: 'Application error',
				enableButton: 0
            });
            errorRef.value = useRoute().params;

            return {
                errorRef
            };
        }
	});
